@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
*{
    font-family: 'Sora', sans-serif;
    box-sizing: border-box;
    margin:0;
    padding:0;


}

body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.slider{
    height: 100vh;
    width: 100%;
}





.containerSliderPrincipal{
    -webkit-user-select: none;
            user-select: none;
    /* box-shadow: 0px 1px 5px 3px rgba(79, 79, 79, 0.5);
    border-radius: 20px; */
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    overflow: hidden;
    margin: auto;
    position: relative;
}
.imgSliderPrincipal{
    pointer-events: none;
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    object-fit: cover;
}
.sliderPrincipal{
    background-color: rgb(24, 24, 26);
    height: 50vw;
    max-height: 100vh;
}
.sliderSection{
    display: none;
}
.carrouselActive{
    display: block;
    visibility: visible;
    -webkit-animation: imgScale linear .4s forwards;
            animation: imgScale linear .4s forwards;
}
@-webkit-keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}
@keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}

.containerBtn{
    position: absolute;
    top: 0;
    cursor: pointer;
    place-items: center;
    display: grid;
    height: 100%;
    width: 30%;
}
.containerBtn p{
    -webkit-user-select: none;
            user-select: none;
    Color: rgba(255, 255, 255, 0.7);
    width: 40px;
    font-size: 60px;
    font-family: monospace;
}
.containerBtn:hover p{
    Color: rgba(255, 255, 255);
}
.sliderbtnLeft{
    left: 0;
}
.sliderbtnRight{
    right: 0;
}
.sliderbtnLeft p{
    position: absolute;
    left: 10px;
}
.sliderbtnRight p{
    position: absolute;
    right: 10px;
    
}
.nivel_selección{
    background-color: gray;
    height: 50vh;
    min-height: 400px;
    display: grid;
    grid-template-columns: 2fr 2fr;
}
.nivel_selección button{
    margin: auto;
    height: 50px;
    width: 150px;
    border-radius: 10px;
    border: none;
}
@media screen and (max-width: 400px) {
    .nivel_selección{
        grid-template-columns: 1fr;
    }
    .nivel_selección button{
        height: 60px;
        width: 200px;
        font-size: 20px;
    }
}
.container{
    background-color: rgb(8, 8, 8);
    padding: 15px 0;
}
.container > h2, .container > h3{
    font-size: 40px;
    color: white;
    text-align: center;
    width: -webkit-max-content;
    width: max-content;
    margin: 10px auto;
}
.container > h3{
    font-size: 30px;
    margin-left: 2.5%;
    text-decoration: underline;
}
.nivelContainer{
    background-color: rgb(40, 40, 40);
    width: 97%;
    margin: auto;
    border: solid 3px black;
    border-radius: 20px;
    overflow: hidden;
    text-align: center;
    color: rgb(194, 188, 180);
}
.nivelContainer .datos{
    width: 95%;
    margin: 10px auto;
    border-radius: 20px;
    display: flex;
}
.imagen{
    display: block;
    width: 30%;
    max-height: 300px;
    margin: auto;
}
.imagen > img{
    max-width: 100%;
    max-height: 300px;
    object-fit: cover;
    border: solid 1px black;
    border-radius: 20px;
}
.nivelSlider .containerSliderPrincipal .sliderPrincipal .sliderSection img{
    height: auto;
    max-height: 300px;
    object-fit: contain;
    border: solid 1px black;
    border-radius: 20px;
    background-color: rgb(24, 24, 26);
    pointer-events: all;
}
.nivelSlider .containerSliderPrincipal .sliderPrincipal{
    background-color: transparent;
    height: 300px;
    max-height: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nivelSlider .containerSliderPrincipal{
    height: 300px;
}




.info{
    padding: 15px 0;
    margin: auto;
    margin-right: 0;
    font-size: 22px;
    width: 70%;
}
.info p, .info h2{
    width: 90%;
    margin: 0 auto;
}
@media screen and (max-width: 950px) {
    .nivelContainer .datos{
        display: block;
    }
    .imagen{
        width: 100%;
    }
    .info{
        width: 100%;
    }
}
